<script>
import { computed, onMounted, reactive, watch,ref } from '@vue/runtime-core'
import {useRoute,useRouter } from 'vue-router';
import axios from 'axios';
import { useStore } from 'vuex';
import { toRaw } from 'vue';


export default {
props:[],
setup(props){

  const route = useRoute()
  const router = useRouter()
  console.log(route.query,'route的query');
  const store = useStore()
  const status = reactive({'selector':false,'page':'1','sort':'0'})
  const details = ref(null)
  const searchList = computed(()=>{
    console.log('这里是searchList-computed',store.state.search.searchList.data);
    return store.state.search.searchList})

 
  const pageList = computed(()=>{
    const tmp = store.getters.doneSearchList({'page':status.page,'sort':status.sort,'page-size':'25'})
    return tmp
  })
  onMounted(()=>{
   store.dispatch('getSearchList',{'page':route.query.page,'search':route.query.search})
 

   
  })
  watch(searchList,()=>{
    console.log('这是watch-searchList');
    console.log(store.state.search.searchList.status);
    if(store.state.search.searchList.status == 200){status.selector = true}
    console.log(status.selector,store.state.search.searchList.data);
})
    

  function changePage(e){
   e.preventDefault()
   for(let element of e.target.parentElement.parentElement.children){
    element.className = ''
   }
   e.target.parentElement.className = "active"
  
    status.page = e.target.innerText
    router.push({name:'search',query:{'search':route.query.search,'page':status.page}})
    console.log(route,'changePage');
    details.value.scrollIntoView()
   

   
  }
 function sort(key,$event){
  for(let element of $event.target.parentElement.parentElement.children){
    element.className = ''
  }
  $event.target.parentElement.className = "active"
  status.sort = key
  
 

 }
 function suiBtn(e){
  e.preventDefault
  e.target.parentElement.previousElementSibling.firstElementChild.classList.toggle("more")


 }
 function getSelected(e){
  e.target.parentElement.classList.toggle('selected')

 }




 function moreBtn(e){
  e.preventDefault()
  e.stopPropagation()
  e.target.parentElement.previousElementSibling.firstElementChild.classList.toggle("extend")
  console.log( e.target.parentElement.previousElementSibling.firstElementChild.style);

 


 }

 //点击图片跳转detail页面，并传入数据
 function detail(key,zoomList,$event){

  let arr  = toRaw(zoomList)
 
  localStorage.setItem('zoomList',JSON.stringify(arr))

  router.push({path:'/goodsDetail',query:{"searchKey":key}})
  
 }

 //hover时展示列表图片
 function displayScroll(src,className,e){
  e = e ||window.event

  let liList = Array.from(e.target.parentElement.children)
  
  liList.forEach(function(item,index,arr){
    item.classList.remove('hover')
  })

  e.target.classList.add('hover')
 
  let el = document.getElementById(className)
  el.setAttribute('src',src)
 }
 
  return {
    status,
    searchList,
    details,
    pageList,
    changePage,
    displayScroll,
    sort,
    suiBtn,
    moreBtn,
    getSelected,
    detail
    
 

  }
  

   
}} 
</script>


